
import { 
  Routes,
  Route, 
  HashRouter} from 'react-router-dom';

import NavBar from './components/navbar';
import Homepage from './pages/homepage';
import About from './pages/about';
import Brothers from './pages/brothers';
import Events from './pages/events';

import '@fontsource/roboto/300.css';
import '@fontsource/roboto/400.css';
import '@fontsource/roboto/500.css';
import '@fontsource/roboto/700.css';

import mobileCheck from './utils/mobileDetect';

import splashImage from './images/PKP-house.jpg';

export default function App() {
  
  const isMobile = mobileCheck()

  return (
    <HashRouter>
      <NavBar></NavBar>
        <img src={splashImage} alt="the fraternity house"
          style={{position:"relative", width:'100vw', height:"auto"}}/>
        { isMobile ? null : 
        <div style={{position:"absolute", top:"30vw", width:"100%", height:"100%"}}>
          <p className="no-select outline-text" style={{fontSize:"5vw", textAlign:"center"}}>
            Pi Kappa Phi - Beta Epsilon Chapter
          </p>  </div>}
     
      <div id="content" 
        style={{position:"relative", width:"80%", margin:"0 10%"}}>
        <Routes>
          <Route path="/" element={<Homepage/>}/>
          <Route path="/about" element={<About/>}/>
          <Route path="/brothers" element={<Brothers/>}/>
          <Route path="/events" element={<Events/>}/>
        </Routes>
      </div>
    </HashRouter>
  )
};
